import React from "react";
import { TextField, Input, InputLabel } from "@material-ui/core";
import { withRouter } from "react-router-dom";
import * as Actions from "../../store/User/UserAction";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import InputAdornment from "@material-ui/core/InputAdornment";
import AccountCircle from "@material-ui/icons/AccountCircle";
import EmailIcon from "@material-ui/icons/Email";
import PhoneIcon from "@material-ui/icons/Phone";
import Button from "@material-ui/core/Button";
import { store } from "react-notifications-component";
import Logo from "../../assets/images/login_page_logo.png";
import formlogo from "../../assets/images/login-form-img.png";
import "../../styles/login.css";
import uploadIcon from "../../assets/images/uploadIcon.png";
import caution from "../../assets/images/caution.svg";
import {serverAddress} from "../../config";
import ReactGA from "react-ga";
// import Analytics from 'analytics'
// import googleAnalytics from '@analytics/google-analytics';

let startTime, endTime;

class Login extends React.Component {
  state = {
    email: "",
    userName: "",
    phoneNumber: "",
    pdf: "",
    selectedFile: null,
    emailError: "",
    phoneNumberError: "",
    userNameError: "",
    pdfError: "",
    jdId: "",
    scheduleID: "",
    uniqueId: "",
    userID: "",
    candidateId: "",
    isDataArrived: true,
    mobile: true,
    laptop: true,
    buttonDisabled: true,
    submitting: "",
    startTime: "",
    endTime: "",
    progress: 0,
    pageDisabled: false,
    testId: "",
    indexDb: null,
    askToUpload: false,
    listfQuestions: [],
    continue: false,
  };

  async componentDidMount() {
    this.props.UserAction.getHealth();
    let request = indexedDB.open("elephantFiles", 2);
    let results = null;
    let thiss = this;
    request.onsuccess = async function (event) {
      try {
        let db = event.target.result;
        let transaction = db.transaction(["videoAnswers"], "readwrite");

        var allRecords = transaction.objectStore("videoAnswers").getAll();
        allRecords.onsuccess = function () {
          results = allRecords.result;
          thiss.setState({ indexDb: results });
        };

        db.onerror = function (event) {
          console.log("Error creating/accessing IndexedDB database");
        };
      } catch (err) {
        console.log("indexdb err", err);
      }
    };

    request.onupgradeneeded = function (event) {
      let db = event.target.result;
      let objectStore = db.createObjectStore("videoAnswers", {
        keyPath: "questionId",
      });
    };
    ReactGA.pageview(`${process.env.REACT_APP_BASEURL}/login`);
    await localStorage.setItem(
      "tokenNew",
      window.location.pathname.split("/").pop()
    );
   
  }

  async componentDidUpdate(prevProps, prevState) {
    const prev = prevProps && prevProps.UserReducer;
    const cur = this.props && this.props.UserReducer;
    if (
      prev.userDataResponse !== cur.userDataResponse &&
      cur.userDataResponse &&
      cur.isUserDataAdded
    ) {
      const { newResponse, response, questions } = cur.userDataResponse;
      await localStorage.setItem("responseId", response && response.id);
      response &&
        response.id &&
        this.props.UserAction.addEvent({
          responseId: response.id,
          event: {
            type: "api",
            level: 1,
            metadata: { detail: "Logged in for interview" },
            timestamp: new Date().toISOString(),
          },
        });
      setTimeout(() => {
        store.addNotification({
          title: "Success",
          message: "Logged in successfully",
          type: "success",
          container: "top-center",
          animationIn: ["animated", "fadeIn"],
          animationOut: ["animated", "fadeOut"],
          dismiss: {
            duration: 2000,
          },
        });

        if (newResponse) {
          let req = indexedDB.deleteDatabase("elephantFiles");
          req.onsuccess = function () {
            console.log("Deleted database successfully");
          };
          req.onerror = function () {
            console.log("Couldn't delete database");
          };
          req.onblocked = function () {
            console.log(
              "Couldn't delete database due to the operation being blocked"
            );
          };
          response &&
            response.id &&
            this.props.UserAction.addEvent({
              responseId: response.id,
              event: {
                type: "debug",
                level: 3,
                metadata: { Page: "Instruction" },
                timestamp: new Date().toISOString(),
              },
            });
          this.props.history.push({
            pathname: "/Instructions",
            state: cur.userDataResponse,
          });
        } else {
          if (this.state.indexDb && this.state.indexDb.length > 0) {
            const { verbalQuestions, mcqQuestions, codingQuestions } = response;
            let newList = verbalQuestions.concat(mcqQuestions);
            newList = newList.concat(codingQuestions);
            let isQuestionThereToUpload = newList.find(
              (question) => question.statusCode != 4
            );
            if (isQuestionThereToUpload) {
              this.setState({ askToUpload: true, listfQuestions: newList });
            }
          } else {
            response &&
              response.id &&
              this.props.UserAction.addEvent({
                responseId: response.id,
                event: {
                  type: "debug",
                  level: 3,
                  metadata: { Page: "Preview" },
                  timestamp: new Date().toISOString(),
                },
              });
            this.props.history.push({
              pathname: "/preview",
              state: cur.userDataResponse,
            });
          }
        }
      }, 500);
      localStorage.setItem("login", true);
    }

    if (prevState.continue !== this.state.continue && this.state.continue) {
      const newList = this.state.listfQuestions;
      const thiss = this;

      this.state.indexDb &&
        this.state.indexDb.length > 0 &&
        this.state.indexDb.map((el) => {
          const quest =
            newList &&
            newList.length > 0 &&
            newList.find((item) => el.questionId === item.id);
          if (quest && quest.statusCode !== 4) {
            var formData = new FormData();
            console.log(el.videoFile);
            formData.append("video", el.videoFile, el.videoFileName);
            if (el.screenFile) {
              formData.append("video", el.screenFile, el.screenFileName);
            }
            formData.append("timeTaken", el.timeTaken);
            el.events && formData.append("events", el.events);
            el.chosenOption && formData.append("chosenOption", el.chosenOption);
            const payload = {
              questionId: el.questionId,
              responseId: localStorage.getItem("responseId"),
              type: el.type,
              questionStatus: el.questionStatus,
              formdata: formData,
            };
            thiss.props.UserAction.updateResponse(payload);

            let request = indexedDB.open("elephantFiles", 2);

            request.onsuccess = function (event) {
              let db = event.target.result;
              let transaction = db.transaction(["videoAnswers"], "readwrite");
              transaction
                .objectStore("videoAnswers", { keyPath: "questionId" })
                .delete(`${el.questionId}`);

              db.onerror = function (event) {
                console.log("Error creating/accessing IndexedDB database");
              };
            };
          }
        });
      this.props.UserAction.addUser();
      this.setState({
        indexDb: null,
      });
      // this.props.history.push({
      //   pathname: "/preview",
      //   state: cur.userDataResponse,
      // });
    }

    if(prev.isGetHealth !== cur.isGetHealth && cur.isGetHealth && cur.health && cur.health.data){
      if( cur.health.data=='OK'){
        this.props.UserAction.getUser();
      }else{
       this.props.history.push({
         pathname:'/maintainance',
         state:{
           errorMessage:cur.health.data
         }
       })
      }
    }

    if(prev.isHealthError !== cur.isHealthError  && cur.isHealthError){
      this.props.history.push({
        pathname:'/maintainance',
        state:{
          errorMessage:'The site is temporarily down for maintainance'
        }
      })
    }
  }

  static getDerivedStateFromProps(nextProps, currentState) {
    if (
      nextProps &&
      nextProps.UserReducer &&
      nextProps.UserReducer.addUserProgress
    ) {
      currentState.progress = nextProps.UserReducer.addUserProgress;
    }

    if (nextProps && nextProps.UserReducer && nextProps.UserReducer.userError) {
      nextProps.UserReducer.userError = "";
      currentState.submitting = "";
    }

    if (
      nextProps &&
      nextProps.UserReducer &&
      nextProps.UserReducer.getUserData &&
      nextProps.UserReducer.getUserData.data &&
      currentState.isDataArrived
    ) {
      currentState.isDataArrived = false;
      if (nextProps.UserReducer.getUserData.data.responsePresent) {
        nextProps.UserAction.addUser();
      }

      currentState.userName =
        nextProps.UserReducer.getUserData.data &&
        nextProps.UserReducer.getUserData.data.candidatename;
      currentState.phoneNumber =
        nextProps.UserReducer.getUserData.data &&
        nextProps.UserReducer.getUserData.data.candidatesphone;
      currentState.email =
        nextProps.UserReducer.getUserData.data &&
        nextProps.UserReducer.getUserData.data.candidateemail;
      currentState.jdId =
        nextProps.UserReducer.getUserData.data &&
        nextProps.UserReducer.getUserData.data.jdId;
      currentState.scheduleID =
        nextProps.UserReducer.getUserData.data &&
        nextProps.UserReducer.getUserData.data.scheduleID;
      currentState.uniqueId =
        nextProps.UserReducer.getUserData.data &&
        nextProps.UserReducer.getUserData.data.uniqueId;
      currentState.userID =
        nextProps.UserReducer.getUserData.data &&
        nextProps.UserReducer.getUserData.data.userID;
      currentState.candidateId =
        nextProps.UserReducer.getUserData.data &&
        nextProps.UserReducer.getUserData.data.candidateId;
      currentState.testId =
        nextProps.UserReducer.getUserData.data &&
        nextProps.UserReducer.getUserData.data.testId;
      currentState.mobile =
        nextProps.UserReducer.getUserData.data &&
        nextProps.UserReducer.getUserData.data.mobile;
      currentState.laptop =
        nextProps.UserReducer.getUserData.data &&
        nextProps.UserReducer.getUserData.data.laptop;

      // if( window.screen.width >1024 && currentState.laptop==false){
      //   window.alert("You are not allowed to attempt this exam from PC/Laptop!")
      // }
      // else if(window.screen.width<=1024 && currentState.mobile==false){
      //   window.alert("You are not allowed to attempt this exam from Mobile/Tab!")
      // }else{
      //   currentState.buttonDisabled=false
      // }

      if (
        (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          navigator.userAgent
        ) ||
          window.screen.width <= 1024) &&
        nextProps.UserReducer.getUserData.data &&
        nextProps.UserReducer.getUserData.data.mobile == false
      ) {
        // window.alert("You are not allowed to attempt this exam from Mobile/Tab!")
        currentState.pageDisabled = true;
      } else if (
        (!/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          navigator.userAgent
        ) ||
          window.screen.width >= 1024) &&
        nextProps.UserReducer.getUserData.data &&
        nextProps.UserReducer.getUserData.data.laptop == false
      ) {
        // window.alert("You are not allowed to attempt this exam from PC/Laptop!")
        currentState.pageDisabled = true;
      } else {
        currentState.buttonDisabled = false;
        currentState.pageDisabled = false;
      }
    }

    return currentState;
  }

  handleClickShowPassword = () => {
    this.setState({ showPassword: !this.state.showPassword });
  };

  handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  handleChange = (e, name) => {
    // console.log(e.target.value, name)
    this.setState({ [name]: e.target.value });
  };

  onFileChange = (e) => {
    if (
      e &&
      e.target &&
      e.target.files &&
      e.target.files[0] &&
      e.target.files[0].name &&
      e.target.files[0].size
    ) {
      const fileSize = Math.round(e.target.files[0].size / 1024);

      let allowedExtensions = /(\.pdf|\.doc|\.docx)$/i;
      // console.log('xyz',fileSize,parseInt(fileSize)<=2048,allowedExtensions.exec(e.target.files[0].name))
      if (
        allowedExtensions.exec(e.target.files[0].name) &&
        parseInt(fileSize) <= 5120
      ) {
        this.setState({ pdf: e.target.files[0], pdfError: "" });
      } else {
        this.setState({
          pdfError:
            "please upload resume in pdf,doc or docx format with size not more than 5 MB",
          pdf: "",
          submitting: "",
        });
      }
    }
  };

  buttonClick = (jdId, scheduleID, uniqueId, userID, candidateId, testId) => {
    if (this.state.pdf) {
      let payload = new FormData();

      payload.append("resume", this.state.pdf);

      localStorage.setItem("loginClicked", true);

      let _this = this;
      var ajax = new XMLHttpRequest();
      ajax.timeout = 1000 * 60 * 10; // time in milliseconds
      startTime = false;
      ajax.upload.addEventListener(
        "progress",
        function progressHandler(event) {
          var percent = (event.loaded / event.total) * 100;
          if (!startTime) {
            startTime = new Date().getTime();
          }
          if (percent >= 98) {
            endTime = new Date().getTime() + 50;
          }
          _this.props.UserAction.addUserProgress({ progress: percent });
        },
        false
      );

      ajax.addEventListener(
        "load",
        function completeHandler(event) {
          if (event.target.status == 201) {
            // console.log(JSON.parse(event.target.responseText))
            _this.props.UserAction.addUserSuccess(
              JSON.parse(event.target.responseText)
            );

            if (!endTime) {
              endTime = new Date().getTime();
            }
            _this.ShowData();
          } else {
            let errorMessage =
              event.target.responseText &&
              JSON.parse(event.target.responseText) &&
              JSON.parse(event.target.responseText).message;
            store.addNotification({
              title: "Error",
              message: errorMessage,
              type: "danger",
              container: "top-center",
              animationIn: ["animated", "fadeIn"],
              animationOut: ["animated", "fadeOut"],
              dismiss: {
                duration: 2000,
              },
            });
            _this.setState({
              submitting: "",
            });
          }
        },
        false
      );

      ajax.addEventListener(
        "error",
        function errorHandler(e) {
          _this.props.UserAction.addUserError({
            error: e.response && e.response.data && e.response.data.message,
          });

          store.addNotification({
            title: "Error",
            message: e.response && e.response.data && e.response.data.message,
            type: "danger",
            container: "top-center",
            animationIn: ["animated", "fadeIn"],
            animationOut: ["animated", "fadeOut"],
            dismiss: {
              duration: 2000,
            },
          });
        },
        false
      );
      ajax.addEventListener(
        "abort",
        function abortHandler(event) {
          // _("status").innerHTML = "Upload Aborted";
        },
        false
      );
      ajax.open("POST", `${serverAddress}/response`);
      ajax.setRequestHeader("Authorization", localStorage.getItem("tokenNew"));
      ajax.send(payload);
      if (!startTime) {
        //startTime = (new Date()).getTime();
      }

      this.setState({
        submitting: "Uploading Resume.Will Redirect....",
        pdfError: "",
      });
    } else {
      this.setState({
        pdfError: "Please Upload Resume",
        submitting: "",
      });
    }
  };

  ShowData = () => {
    let duration = (endTime - startTime) / 1000;
    let bitsLoaded = this.state.pdf && this.state.pdf.size * 8;
    console.log(
      startTime,
      endTime,
      bitsLoaded,
      duration,
      "SPEED IN mbps",
      bitsLoaded / duration / (1024 * 1024)
    );
    let speedMbps = (bitsLoaded / duration / 1024 / 1024).toFixed(2);
    // alert("Speed: " + speedMbps + " Mbps");
    localStorage.setItem("uploadSpeed", speedMbps);
  };

  render() {
    let cautionImage = <img src={caution} />;

    return this.state.askToUpload ? (
      <div className='MessageDiv'>
        <div className='birbal-logo1'>
          <img src={Logo} alt='logo1'></img>
        </div>
        <div className='messageArea'>
          <div>
            We have found your partial Submission Do you want to continue with
            your submission
          </div>
          <div className='button-div'>
            <Button
              variant='contained'
              className='Button'
              onClick={() => this.setState({ continue: true })}
            >
              Yes
            </Button>
          </div>
        </div>
      </div>
    ) : !this.state.pageDisabled ? (
      <div className='log-home'>
        <div className='container'>
          <div className='div-wrapper'>
            <div className='div-1-wrapper'>
              <div className='birbal-logo'>
                {" "}
                <img src={Logo} alt='logo'></img>
              </div>
              <div className='login_instr'>
                <h6 className='mb-50'>Basic Instructions</h6>
                <div className='list'>
                  <div className='point-wrapper'>
                    <div className='bullet'></div>
                    <div className='point-content'>
                      {" "}
                      <p>Please ensure that you are on a laptop or desktop.</p>
                    </div>
                  </div>
                  <div className='point-wrapper'>
                    <div className='bullet'></div>
                    <div className='point-content'>
                      {" "}
                      <p>Ensure your internet connection is stable.</p>
                    </div>
                  </div>
                  <div className='point-wrapper'>
                    <div className='bullet'></div>
                    <div className='point-content'>
                      {" "}
                      <p>
                        Check your headphones before using them. Your mic should
                        be working.
                      </p>
                    </div>
                  </div>
                  <div className='point-wrapper'>
                    <div className='bullet'></div>
                    <div className='point-content'>
                      {" "}
                      <p>
                        Please ensure that the audio quality of your laptop /
                        desktop is good.
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className='div-2-wrapper'>
              <div className='birbal-logo-new'>
                {" "}
                <img src={Logo} alt='logo'></img>
              </div>
              <div>
                <div className='login-form'>
                  <div className='img-login'>
                    <img className='log-img' src={formlogo}></img>
                    <h2 className='login-bold-text'>Login</h2>
                  </div>
                  <div className='card'>
                    <form className='input-form'>
                      <div class='form-group'>
                        <label for='exampleFormControlInput1'>Full Name</label>
                        <TextField
                          type='text'
                          class='form-control'
                          disabled='true'
                          id='exampleFormControlInput1'
                          value={this.state.userName}
                          onChange={(e) => this.handleChange(e, "userName")}
                          InputProps={{
                            startAdornment: (
                              <InputAdornment position='start'>
                                <AccountCircle />
                              </InputAdornment>
                            ),
                          }}
                        />
                      </div>
                      <div class='form-group'>
                        <label for='exampleFormControlInput1'>Email</label>
                        <TextField
                          type='email'
                          class='form-control'
                          disabled='true'
                          id='exampleFormControlInput1'
                          value={this.state.email}
                          onChange={(e) => this.handleChange(e, "email")}
                          InputProps={{
                            startAdornment: (
                              <InputAdornment position='start'>
                                <EmailIcon />
                              </InputAdornment>
                            ),
                          }}
                        />
                      </div>
                      <div class='form-group'>
                        <label for='exampleFormControlInput1'>
                          Phone Number
                        </label>
                        <TextField
                          type='text'
                          class='form-control'
                          id='exampleFormControlInput1'
                          value={this.state.phoneNumber}
                          disabled='true'
                          onChange={(e) => this.handleChange(e, "phoneNumber")}
                          InputProps={{
                            startAdornment: (
                              <InputAdornment position='start'>
                                <PhoneIcon />
                              </InputAdornment>
                            ),
                          }}
                        />
                      </div>

                      <div>
                        Resume
                        <br />
                        {/* <input id="fileInput" type="file" name="file" accept="application/pdf" onChange={(e)=>this.onFileChange(e)}/>
                         */}
                        <div
                          className='upload-photo'
                          style={{ cursor: "pointer" }}
                          onClick={() =>
                            // `current` points to the mounted file input element
                            this.refs.fileUploader.click()
                          }
                        >
                          <div className='flex-div'>
                            <img src={uploadIcon} />
                            <input
                              type='file'
                              id='file'
                              ref='fileUploader'
                              style={{ display: "none" }}
                              accept='application/pdf,application/msword,
               application/vnd.openxmlformats-officedocument.wordprocessingml.document'
                              onChange={(e) => this.onFileChange(e)}
                            />

                            <div className='text-upload'>
                              Upload Your Resume{" "}
                            </div>
                          </div>
                        </div>
                        <div className='text-upload'>
                          {this.state.pdf && this.state.pdf.name
                            ? this.state.pdf.name
                            : null}
                        </div>
                        {this.state.pdfError ? (
                          <div className='error'>{this.state.pdfError}</div>
                        ) : null}
                        {this.state.submitting ? (
                          <div style={{ fontSize: "13px", color: "#7A7A7A" }}>
                            <div
                              style={{
                                display: "flex",
                                justifyContent: "center",
                                alignItems: "center",
                                padding: "5px 0",
                              }}
                            >
                              <div style={{ marginRight: "5px" }}>
                                {cautionImage}
                              </div>
                              <div> {this.state.submitting}</div>
                            </div>
                            <div style={{ textAlign: "center" }}>
                              {this.state.progress.toFixed(1)}%
                            </div>
                          </div>
                        ) : null}
                      </div>
                    </form>

                    <div className='button-div'>
                      <Button
                        variant='contained'
                        className='Button'
                        disabled={this.state.buttonDisabled}
                        onClick={() =>
                          this.buttonClick(
                            this.state.jdId,
                            this.state.scheduleID,
                            this.state.uniqueId,
                            this.state.userID,
                            this.state.candidateId,
                            this.state.testId
                          )
                        }
                      >
                        Login
                      </Button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    ) : this.state.mobile == false ? (
      <div>Please use a Laptop/PC to continue with the Interview process!</div>
    ) : this.state.laptop == false ? (
      <div>Please use a Mobile/Tab to continue with the Interview process!</div>
    ) : null;
  }
}

const mapStateToProps = (state) => ({
  UserReducer: state.UserReducer,
});

function mapDispatchToProps(dispatch) {
  return {
    UserAction: bindActionCreators(Actions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Login));
